* {
  font-family: 'Changa', sans-serif;
}

.ml-auto{
  margin-left: auto;
}
.box-shadow{
box-shadow: 2px 0px 1px 1px #8c8c8c5e;
}

.navbar-light .navbar-nav .nav-link {
  color: #000000;
  font-weight: 500;
}

.navbar-light .navbar-brand {
  color: #ac0010;
  font-size: 30px;
  font-weight: 600;
}

  .navbar-expand-lg .navbar-nav .nav-link {
      padding-right: 1rem;
      padding-left: 1rem;
      font-size: 16px;
      font-weight: 600;
}


.catcard .card-img, .card-img-top {
  border-top-left-radius: calc(.25rem - 1px);
    border-top-right-radius: calc(.25rem - 1px);
    max-width: 70px;
    margin: auto;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 50%;
}



.catcard.card.h-100 {
  padding: 10px;
    margin-top: 10px;
    border: none;
    text-align: center;
    box-shadow: 2px 0px 4px 1px #b5b5b5;
   
  
}

.card-title {
  margin-bottom: .5rem;
  font-size: 16px;
  font-weight: 600;
  color: #ac0010;
}

.about-us-bar{
  padding-top: 50px;
  padding-bottom: 50px;
  margin-top: 25px;
}

.heading{
  color: #ad2923;
  margin-bottom: 25px;
}

.about-us-text{
  margin: auto;
}

.about-us-text li {
  margin-bottom: 15px;
  border-bottom: 1px dashed #ccc;
  padding-bottom: 15px;
}

img{
  max-width: 100%;
  max-height: 100%;
}

.why-us img{
  border-radius: 3px;
  max-height: 350px;
  margin: auto;
}

.why-us .heading{
  margin-bottom: 50px;
}

.bg-light{
  background: #fff;
}

.client-logo {
  display: inline-block;
  width: 25%;
  padding: 10px 20px;
  text-align: center;
  height: 110px;
  clear: both;
  float: unset;
}

.center-align{
  align-items: center;
}

/* Set the size of the div element that contains the map */
#map {
  height: 400px;
  /* The height is 400 pixels */
  width: 100%;
  /* The width is the width of the web page */
}


.category-bar{
  margin-top: 15px;
}

.category-bar  .col{
  padding: 3px;
  margin: 0;
}

@media only screen and (max-width: 600px) {
  .client-logo {
    width: 50%;
  }
}